<template>
  <div>
    <headers />
    <div class="main">
      <img class="sh" src="../../public/assets/picc.png" />
      <form>
        <input
          type="text"
          placeholder="First name"
          name="firstName"
          v-model="firstName"
        />
        <input
          type="text"
          placeholder="Surname"
          name="surname"
          v-model="surname"
        />
        <input
          type="text"
          placeholder="Other names"
          name="otherName"
          v-model="otherName"
        />
        <input type="email" placeholder="E-mail" name="email" v-model="email" />
        <input
          type="password"
          placeholder="Password"
          name="password"
          v-model="password"
        />
        <input
          type="password"
          placeholder="Confirm password"
          v-model="confirmPassword"
        />
        <button v-on:click="signUp">Submit</button>
      </form>
    </div>
    <p>
      Already have an account?
      <router-link to="/login">Login here.</router-link>
    </p>
    <footer2 />
  </div>
</template>

<script>
//import { mapGetters, mapActions } from 'vuex'
import headers from '../components/headex.vue'
import footer2 from '../components/footer2.vue'

export default {
  name: "signup",
  data() {
    return {
      id: "",
      firstName: "",
      surname: "",
      otherName: "",
      email: "",
      password: "",
      confirmPassword: "",
    };
  },
  computed: {
    //...mapGetters("users", ["userInfo"])
  },
  components: {
    headers,
    footer2
  },
  methods: {
    async signUp() {
      const userData = {
        id: Math.floor(Math.random() * 1000),
        firstName: this.firstName,
        surname: this.surname,
        otherName: this.otherName,
        email: this.email,
        password: this.password,
        confirmPassword: this.confirmPassword,
      };
      const res = await fetch("https://my-json-server.typicode.com/olatunji-20/zeekollection/users", {
        method: "POST",
        headers: {
          "Content-type": "application/json",
        },
        body: JSON.stringify(userData),
      });
      const data = await res.json();
      console.log("data", data);
    },
    //...mapActions("users", ["signUp"])
  },
};
</script>

<style scoped>
.main {
  width: 45%;
  height: auto;
  background: #ffe6ff22;
  margin: 50px auto 10px;
  padding: 20px 10px;
  border-radius: 3px;
}
.sh {
  width: 170px;
  height: 170px;
  border: 1px solid #ffe6ff99;
  margin: 30px auto 40px;
  border-radius: 50%;
}
input {
  width: 43%;
  height: 35px;
  margin: 10px 10px;
  text-indent: 15px;
  border-radius: 5px;
  border: 1px solid #ffe6ff99;
}
input:focus {
  outline: none;
  border: 1px solid hotpink;
}
button {
  height: 30px;
  width: 50%;
  background: hotpink;
  color: white;
  border: 1px;
  margin: 40px auto;
  border-radius: 5px;
}
p {
  font-size: 14px;
  margin-bottom: 80px;
}
span {
  color: teal;
}

@media screen and (max-width: 900px) {
  .main {
    width: 60%;
  }
}

@media screen and (max-width: 450px) {
  .sh {
    width: 100px;
    height: 100px;
  }
  .main {
    width: 90%;
  }
  input {
    width: 80%;
  }
}
</style>